      this.parameterFn();
    },
    childFn(){
      console.log("");  
    },
    change(){
      this.$emit('change-value', "")
    },
    clear(){
      this.$store.dispatch(USER_CLEAR);
    }
  },
  created () {


  },
  components: {
 	
  }
}
</script>

