export default function(router, store) {
  router.beforeEach((to, from, next)=>{
    /*
      url
    */
    if (to.meta.auth) {
      if (store.getters.user.token !== null && store.getters.user.token !== '') {
        next();
      } else {
        next({
          path: '/login',
          query: {redirect: to.fullPath}  // path
        })
      }
    } else {
      next();
    }
  })
}



