//
export function getKernelVersion() {
  let u = navigator.userAgent;
  return {
    webKit: u.indexOf('AppleWebKit') > -1, //
    mobile: !!u.match(/AppleWebKit.*Mobile.*/) || !!u.match(/AppleWebKit/), //
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
    android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
    iPhone: u.indexOf('iPhone') > -1 || u.indexOf('Mac') > -1, //iPhoneQQHD
    iPad: u.indexOf('iPad') > -1, //iPad
    weChat: u.match(/MicroMessenger/i) != null //
  }
}

//
export function  getUrlParams() {
  console.log(window.location.href)
  if (window.location.href.indexOf('?') === -1) return {}
  let urlParams = window.location.href.split('?')[1];
  let paramsArr = urlParams.split('&');
  let keyv = paramsArr.map(v => v.split('='));
  let p = {};
  keyv.map(v => {
    p[v[0]] = v[1]
  })
  return p
}

   


